import React from "react";
import { Link } from "react-router-dom";

const AdminDashboard = () => {
  return (
    <div className="container mt-5">
      <h1>لوحة تحكم المسؤول</h1>
      <div className="row mt-4">
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">إدارة المستخدمين</h5>
              <p className="card-text">تحكم في حسابات المستخدمين المسجلين.</p>
              <Link to="/admin/manage-users" className="btn btn-primary">
                الدخول
              </Link>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">إدارة المحتوى</h5>
              <p className="card-text">تحكم في المدونات والمشاريع والأخبار.</p>
              <Link to="/admin/manage-content" className="btn btn-primary">
                الدخول
              </Link>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">الإحصائيات</h5>
              <p className="card-text">عرض إحصائيات وتحليلات الموقع.</p>
              <Link to="/admin/statistics" className="btn btn-primary">
                الدخول
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;
